button {
  height: -webkit-min-content;
  height: min-content;
  font-size: medium;
  /* width: 130px; */
  background-color: rgba(0, 217, 159, 0.75);
  border: none;
  color: white;
  cursor: pointer;
  padding: 5px;
  padding-left: 13px;
  padding-right: 13px;
  padding-top: 8px;
  padding-bottom: 8px;
  border-radius: 5px;
  margin-top: 2;
  margin: 2px;
  font-family: "Courier New", Courier, monospace;
}

button:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

