.node-wall {
  background-image: url(data:image/png;base64,);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-animation-name: wallAnimation;
          animation-name: wallAnimation;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0ms;
          animation-delay: 0ms;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes wallAnimation {
  0% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    background-color: rgba(0, 32, 42, 0.58);
  }

  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 32, 42, 0.58);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(69, 71, 71, 0.58);
  }
}

@keyframes wallAnimation {
  0% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    background-color: rgba(0, 32, 42, 0.58);
  }

  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 32, 42, 0.58);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(69, 71, 71, 0.58);
  }
}

.node-visited {
  border-radius: 50%;
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0ms;
          animation-delay: 0ms;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    /* background-color: rgba(0, 0, 66, 0.75); */
    background-color: #935ed3;
    border-radius: 100%;
  }

  50% {
    /* background-color: rgba(17, 104, 217, 0.75); */
    background-color: #ab8ab9;
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}

@keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    /* background-color: rgba(0, 0, 66, 0.75); */
    background-color: #935ed3;
    border-radius: 100%;
  }

  50% {
    /* background-color: rgba(17, 104, 217, 0.75); */
    background-color: #ab8ab9;
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}

.node-shortest-path {
  border-radius: 50%;
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0ms;
          animation-delay: 0ms;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(48, 197, 67);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(48, 197, 67);
